import React, { useMemo, useState, useEffect } from "react";
import axios from "axios";

import Table from "./Table";
import "./App.css";

function App() {
  const columns = useMemo(
    () => [
      {
        Header: "Office tools",
        columns: [
          {
            Header: "Name",
            accessor: "name"
          },
          {
            Header: "State",
            accessor: "state"
          },
          {
            Header: "Date of purchase",
            accessor: "dateOfPurchase.date",
            Cell: ({ cell: { value } }) => {
              const custom_date = value.substr(0, 10)
              return <span>{custom_date}</span>
            }
          }
        ]
      },
      {
        Header: "Outsourced",
        columns: [
          {
            Header: "Person(s)",
            accessor: "worker.name",
          }
        ]
      }
    ],
    []
  );

  const [data, setData] = useState([]);

  useEffect(() => {
    (async () => {
      const result = await axios("http://localhost:8080/tools.php");
      setData(result.data);
    })();
  }, []);

  return (
    <div className="App">
      <Table columns={columns} data={data} />
    </div>
  );
}

export default App;
